.result-container {
  background-color: #ECC12D;
  overflow: hidden;
  text-align: right;
  padding: 0;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  border-bottom: 1px solid #F3F6F5;
  border-radius: 5px 5px 0 0;
}

.result-container h1 {
  font-size: 2em;
  font-family: 'Oswald', sans-serif;
  color: #F3F6F5;
  margin: 3px 10px 0 0;
  padding: 0;
}

.result-container p {
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin: -5px 5px 8px 0;
  padding: 0 5px;
}



